import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/grupoHistorico';

const GrupoHistoricoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      formProps={{ ...props.formProps }}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descriçao',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'GrupoHistoricoSearchView',
      }}
      label={props.label ? props.label : 'Grupo Histórico'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Grupo Histórico'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default GrupoHistoricoIS;
