import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Form, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  dispatchClose,
  EditViewProps,
  ErroList,
  ERROR,
  ProcessBar,
  progressActions,
  showToast,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { RuleMessage } from '../../../../models/dtos/ger/ruleMessage';
import { ImportacaoHistorico } from '../../../../models/dtos/mcg/importacaoHistorico';
import { importarHistorico } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import GrupoHistoricoIS from '../../../components/ger/grupoHistoricoIS/GrupoHistoricoIS';
import ResultadoIntegracao from '../../../components/resultadoIntegracao/ResultadoIntegracao';

interface ImportacaoHistoricoProcessViewProps extends EditViewProps<ImportacaoHistorico> {}

const ImportacaoHistoricoProcessView: FC<ImportacaoHistoricoProcessViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const [showResultView, setShowResultView] = useState(false);

  const [listResult, setListResult] = useState<RuleMessage[]>([]);

  const dispatch = useDispatch();

  const formProps = useForm<ImportacaoHistorico>({ mode: 'onBlur' });

  useEffect(
    () => {
      if (globalReducer.globalParameter.grupoHistorico !== null) {
        const { grupoHistorico } = globalReducer.globalParameter;
        setTimeout(() => {
          formProps.setValue('grupoHistoricoDestino', grupoHistorico);
        }, 500);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  const handleImport = (data: ImportacaoHistorico) => {
    const erros = validateExtras();
    if (erros.length > 0) {
      const params: AlertProps = {
        message: <ErroList erros={erros} />,
        type: AlertType.WARNING,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      dispatch(
        progressActions.showProgress({
          animated: true,
          className: 'progress-bar-rounded progress-animated-alt',
          color: 'first',
          label: 'Aguarde processando...',
          striped: true,
          value: 100,
        })
      );
      callImport(data);
    }
  };

  function callImport(data: ImportacaoHistorico): void {
    importarHistorico(data, {
      errorFunction: mensagem => {
        dispatch(progressActions.hideProgress());
        showToast(mensagem, ERROR);
      },
      thenFunction: (result: RuleMessage[]) => {
        dispatch(progressActions.hideProgress());
        setListResult(result);
        setShowResultView(true);
      },
    });
  }

  function validateExtras(): string[] {
    const erros = [];
    if (formProps.getValues().grupoHistoricoOrigem) {
      if (formProps.getValues().grupoHistoricoOrigem.id === formProps.getValues().grupoHistoricoDestino.id) {
        erros.push('O Grupo de Histórico de Origem não pode ser igual ao Grupo de Histórico de Destino');
      }
    }

    return erros;
  }

  return (
    <WindowComponent
      title="Importação de Histórico"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMCG(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={2191}
    >
      {showResultView ? (
        <ResultadoIntegracao
          {...props}
          errorList={listResult.filter(it => !it.success)}
          successList={listResult.filter(it => it.success)}
          hidePrintButton={true}
          tabFailure="Registros Rejeitados"
          tabSuccess="Registros Aceitos"
        />
      ) : (
        <Form>
          <GrupoHistoricoIS
            disabled={true}
            formProps={{ ...formProps, validation: { required: true } }}
            id="grupoHistoricoDestino"
            label="Grupo de Histórico de Destino"
          />
          <Row>
            <Col>
              <GrupoHistoricoIS
                formProps={{ ...formProps, validation: { required: true } }}
                id="grupoHistoricoOrigem"
                label="Grupo de Histórico de Destino"
                placeholder="Informe o Grupo de Histórico de Destino"
              />
            </Col>
          </Row>
          <ProcessBar handleSubmit={formProps.handleSubmit(handleImport)} handleClose={handleCancel} />
        </Form>
      )}
    </WindowComponent>
  );
};

export default ImportacaoHistoricoProcessView;
